/* 메인 2 */

.main_2_Day {
    display: flex;
    flex-flow: row wrap;
}

.main_2_Day ul {
    width: 230px;
}
.main_2_Day ul li {
    width: 200px;
    margin-left: 20px;
}

.main_2_Day h2 {
    font-weight: 900;
    padding: 20px;
    font-size: 18px;
    width: 960px;
}

.main_2_Day h3 {
    padding: 8px;
    padding-left: 0;
    font-weight: bold;
    font-size: 14px;
}

.main_2_Day h4 {
    color: #000;
    font-size: 13px;
}
.main_2_Day p {
    color: #929292;
    padding-top: 8px;
    font-size: 13px;
    line-height: 1.5;
}


/* 메인 2 이미지 */
.main_2_Day ul li div {
    overflow: hidden;
}
.main_2_Day ul li img {
    scale: 2;
}