.detail {
    box-shadow: 0 13px 24px -5px rgba(50,50,93,0.25), 0 8px 16px -5px rgba(0,0,0,0.3), 0 -6px 16px -5px rgba(0,0,0,0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: #fff;
    margin: 100px auto;
    width: 100%;
    max-width: 600px;
    font-weight: 300;
    position: relative;
}
.detail p:nth-child(1) {
    line-height: 1.5;
    color: #234049;
    font-size: 40px;
    margin-bottom: 20px;
}
.detail p:nth-child(3) {
    font-weight: 600;
    padding: 20px;
    color: rgb(23, 141, 111);
}
.detail h2 {
    font-size: 13px;
    font-weight: 500;
    line-height: 1.5;
    color: #929292;
}

.detail p:nth-child(5) {
    position: absolute;
    right: 20px;
    top: 30px;
    z-index: 1;
    cursor: pointer;

}